.project-details {
	margin: 0 auto;
	max-width: 865px;

	display: flex;
	flex-direction: column;
	align-items: center;

	text-align: center;
}

.project-details__cover {
	max-width: 800px;
	margin-bottom: 40px;
	cursor: pointer;
	box-shadow: var(--box-shadow);
	border-radius: 10px;
}

.project-details__desc {
	margin-bottom: 30px;
	font-weight: 700;
	font-size: 24px;
	line-height: 1.3;
}
.github__link {
	display: flex;
	border: 1px solid black;
	padding: 10px 15px;
	align-items: center;
	background-color: white;
	border-radius: 5px;
	transition-duration: .3s;
}
.github__link:hover {
	opacity: 0.7;
}
.github__link img {
	margin-right: 10px;
}
.github__link p {
	color: black;
	font-size: 16px;
}
